<template>
  <v-footer class="footer accent text-center">
    <v-card-text class="pt-10">
      <v-tooltip top>
        <template v-slot:activator="{ on }">
          <v-btn icon @click="home()" v-on="on">
            <v-icon color="grey lighten-1">mdi-home</v-icon>
          </v-btn>
        </template>
        <span color="secondary">Home</span>
      </v-tooltip>
      <v-tooltip top>
        <template v-slot:activator="{ on }">
          <v-btn icon @click="about()" v-on="on">
            <v-icon color="grey lighten-1">mdi-help</v-icon>
          </v-btn>
        </template>
        <span color="secondary">About</span>
      </v-tooltip>
      <v-tooltip top>
        <template v-slot:activator="{ on }">
          <v-btn icon @click="map()" v-on="on">
            <v-icon color="grey lighten-1">mdi-map</v-icon>
          </v-btn>
        </template>
        <span color="secondary">Map</span>
      </v-tooltip>
    </v-card-text>

    <v-divider></v-divider>

    <v-card-text class="white--text">
      {{ new Date().getFullYear() }} — <strong>BeagleNet</strong>
    </v-card-text>
  </v-footer>
</template>

<script>
export default {
  data() {
    return {
      on: false
    };
  },
  methods: {
    async map() {
      await this.$router.push("/map").then((this.drawer = false));
    },
    async about() {
      if (this.$route.matched.some(({ name }) => name === "home")) {
        this.$vuetify.goTo(`#About`, this.options);
        this.drawer = false;
      } else {
        await this.$router
          .push("/")
          .then(() => {
            this.$vuetify.goTo(`#About`, this.options);
          })
          .then((this.drawer = false));
      }
    },
    async home() {
      this.$router.push("/");
      await this.$vuetify
        .goTo(`#Home`, this.options)
        .then((this.drawer = false));
    }
  }
};
</script>

<style scoped>
.footer {
  margin-top: 20vh;
  width: 100vw;
  max-width: 100vw;
  /* bottom: 100vh; */
}
</style>
